// src/details/File3.js
import { useState, useEffect } from 'react';
import { Tab, Tabs } from 'react-bootstrap';
import DataTable from 'react-data-table-component';
import Papa from 'papaparse';
//import CsvTable from '../components/CsvTable';

const File3 = () => {
    const [key, setKey] = useState('detail');
    const [pathwayData, setPathwayData] = useState([]);

    useEffect(() => {
        fetch('/Covid19_datastructure/data/bulk_pathway.csv')
            .then((response) => response.text())
            .then((text) => {
                Papa.parse(text, {
                    header: true,
                    skipEmptyLines: true,
                    complete: (results) => {
                        setPathwayData(results.data);
                    },
                });
            });
    }, []);

    const columns = pathwayData.length > 0 ? Object.keys(pathwayData[0]).map(header => ({
        name: header,
        selector: row => row[header],
        sortable: true,
    })) : [];

    return (
        <div>
            <h2>Pathway</h2>
            <hr />
            <h4>Descriptions</h4>
            <p>Detailed information about Pathway.</p>
            <br />
            <h4>Data Structure</h4>
            <h5>Features</h5>
            <h4>Data Distribution</h4>

            <h4>Files information</h4>
            <Tabs
                defaultActiveKey="detail"
                id="file-info"
                className="mb-3"
                activeKey={key}
                onSelect={(k) => setKey(k)}
                justify
                >
                <Tab eventKey="detail" title="Detail">
                    {pathwayData.length > 0 ? (
                        <DataTable
                            columns={columns}
                            data={pathwayData}
                            pagination
                            paginationPerPage={5}
                            paginationRowsPerPageOptions={[5, 10, 15, 20]}
                        />
                    ) : (
                        <div>Loading...</div>
                    )}
                </Tab>
                <Tab eventKey="compact" title="Compact">
                    Compact detail of this file
                </Tab>
                <Tab eventKey="column" title="Column">
                    Column values of the file
                </Tab>
            </Tabs>

        </div>
    );
};

export default File3;
